.item {
  overflow: hidden;
  display: grid;
  grid-template-columns: 1;
  grid-template-rows: 1;
}

.item img {
  grid-column: 1 / -1;
  grid-row: 1 / -1;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.item:hover .itemOverlay{
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0)
}

.itemOverlay {
  background-color: var(--greyOverlay);
  grid-row: 1 / -1;
  grid-column: 1 /-1;
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  display: grid;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.item.v2 {
  grid-row: span 2;
}

.item.v3 {
  grid-row: span 3;
}

.item.v4 {
  grid-row: span 4;
}

.item.h2 {
  grid-column: span 2
}

.item.h3 {
  grid-column: span 3
}

.item.h4 {
  grid-column: span 4;
}

.galleryContainer {
  width: 80vw;
  margin: auto;
  margin-bottom: 200px;
  display: grid;
  grid-template-columns: repeat(auto-fit, 100px);
  grid-auto-rows: 100px;
  grid-auto-flow: dense;
  -ms-flex-pack: center;
      justify-content: center;
}

.mainOverlay {
  display: none;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 2;
}

.overlayImage {
  width: 600px;

  -o-object-fit: cover;

     object-fit: cover;
}

.itemOverlayButton {
  width: 70px;
  background-color: rgba(0,0,0,0.7);
  border: 2px solid white;
  color: white;
  padding: 5px;
  font-size: 10px;
}

.imageCloseButton {
  width: 80px;
  background-color: rgba(0,0,0,0.7);
  border: 2px solid white;
  color: white;
  padding: 5px;
  position: absolute;
  margin: 40px;
  top: 0;
}

.mainOverlayOpen {
  display: grid;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}



