.menuBarContainer {
  height: 50px;
  width: 100vw;
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 20px;
  position: fixed;
  color: white;
  z-index: 2;
  top: 0;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 3
}

.menuBarButton {
  color: white;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  padding: 3px 0 ;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 15px;
  font-weight: normal;
  text-align: center;
  border-radius: 5px;
  padding: 5px;
}

.menuBarButton::after {
  content:'';
  display: block;
  width: 0;
  height: 1px;
  background-color: lightgrey;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s
}

.menuBarButton:hover {
  color: #00a1f1;
  cursor: pointer;
}

.menuBarButton:hover::after {
  width: 100%;
}

.logo {
  position: absolute;
  left: 20px;
  top: 20px;
  background-color: white;
  width: 40px;
  height: 59px;
  border-radius: 3px
}

.logo img {
  height: 40px
}

.logo h1 {
  font-size: 9px;
  color: orange;
  margin: 0;
  text-align: center;
  height: 10px
}

.logo:hover {
  cursor: pointer
}



